<template>
    <div>
        <!-- Table All Product -->
        <vx-card>
        <vs-table stripe search pagination  max-items="10" :data="products">
            <template slot="header" class="mb-5">
                <h1 class="mr-2">Data Produk</h1>
                <vx-tooltip text="Tambah Produk" style="display:inline">
                    <vs-button @click="addProduct" radius icon="create" color="success" type="flat" size="large" ></vs-button>
                </vx-tooltip>
            </template>

            <template slot="thead">
                <vs-th sort-key="sku" width="130px" class="bg-primary text-white">
                Sku
                </vs-th>
                <vs-th sort-key="barang" class="bg-primary text-white">
                Nama
                </vs-th>
                <vs-th sort-key="kategori"  width="130px" class="bg-primary text-white">
                Kategori
                </vs-th>
                <vs-th sort-key="aksi" width="130px" class="bg-primary text-white text-center">
                <p class="w-full">Aksi</p>
                </vs-th>
            </template>

            <template slot-scope="{data}">
                <vs-tr :key="items.kode" v-for="items in data" >
                    <vs-td :data="items.sku">
                        {{items.sku}}
                    </vs-td>
                    <vs-td :data="items.barang">
                        {{items.barang}}
                    </vs-td>
                    <vs-td :data="items.kategori">
                        {{items.kategori}}
                    </vs-td>
                    <vs-td :data="items.aksi" class="text-center">
                    <vx-tooltip text="Ubah Produk" style="display:inline">
                        <vs-button @click="editData(items.kode, items.sku, items.barang, items.kategori, items.kode_kategori, items.img)" class="mr-1" color="warning" type="filled" size="small" icon="create" style="display:inline"></vs-button>
                    </vx-tooltip>
                    <vx-tooltip text="Harga Produk" style="display:inline">
                        <vs-button @click="popupHargaBtn(items.kode, items.barang)" class="mr-1" color="#0fabbc" type="filled" size="small" icon="payments" style="display:inline"></vs-button>
                    </vx-tooltip>
                    <vx-tooltip text="Ringkasan Produk" style="display:inline">
                        <vs-button @click="popupDataSummaryBtn(items.kode, items.sku, items.barang)" color="success" type="filled" size="small" icon="poll" style="display:inline"></vs-button>
                    </vx-tooltip>
                    </vs-td>
                </vs-tr>
            </template>
        </vs-table>
        </vx-card>

        <!-- Popup Add Product -->
        <vs-popup class="holamundo"  title="Tambah Produk" :active.sync="addNew">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Sku" v-validate="'required'" name="form.sku" data-vv-validate-on="blur" v-model="form.sku" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama" v-validate="'required'" name="form.nama" data-vv-validate-on="blur" v-model="form.nama" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <small class="ml-1">Kategori</small><br>
                    <v-select :placeholder="'-- Pilih Kategori --'" label="kategori" :options="kategoriAll" v-validate="'required'" name="form.kode_barang_kategori" data-vv-validate-on="blur" v-model="form.kode_barang_kategori"  ></v-select>
                    <!-- <vs-input class="w-full" label="Kategori" v-model="form.kode_barang_kategori" /> -->
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <small class="ml-1">Foto</small><br>
                        <img class="img-default my-2" :src="form.img">
                        <vs-input type="file" @change="handleImage" accept="image/*"  />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button class="mr-3 mb-2" :disabled="!validateFormAdd" color="success" @click=" addData()">Simpan</vs-button>
                    </div>
                </div>
        </vs-popup>

        <!-- Popup Edit Product -->
        <vs-popup class="holamundo"  title="Ubah Produk" :active.sync="Edit">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Sku" v-model="form.sku" v-validate="'required'" name="form.sku" data-vv-validate-on="blur" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama" v-model="form.barang" v-validate="'required'" name="form.barang" data-vv-validate-on="blur" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <small class="ml-1">Kategori</small><br>
                    <v-select :placeholder="'-- Pilih Kategori --'" :reduce="kategori => kategori.kode" label="kategori" :options="kategoriAll" v-validate="'required'" name="form.kode_kategori" data-vv-validate-on="blur" v-model="form.kode_kategori"  ></v-select>
                    <!-- <vs-input class="w-full" label="Kategori" v-model="form.kategori" /> -->
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <small class="ml-1">Foto</small><br>
                        <img class="img-default my-2" :src="form.img" >
                        <vs-input type="file" @change="handleImage" accept="image/*"  />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" class="mr-3 mb-2" :disabled="!validateFormEdit" @click="updateData(form.kode)">Simpan</vs-button>
                    </div>
                </div>
        </vs-popup>

        <!-- Popup Harga Product Item -->
        <vs-popup fullscreen title="Harga Produk" :active.sync="popupHarga">
            <vs-table stripe search pagination  max-items="10" :data="hargaProduct">
                <template slot="header" class="mb-5">
                    <h3 class="mr-5">{{judulHarga}}</h3>
                    <vx-tooltip text="Tambah Harga Produk" style="display:inline">
                        <vs-button @click="addHargaProduct" radius icon="create" color="success" type="flat" size="large"></vs-button>
                    </vx-tooltip>
                </template>

                <template slot="thead">
                    <vs-th sort-key="wilayah" class="bg-primary text-white">
                    Wilayah
                    </vs-th>
                    <vs-th sort-key="harga_stockpoint"  width="120px" class="bg-primary text-white">
                    Harga Stockpoint
                    </vs-th>
                    <vs-th sort-key="harga_reseller"  width="120px" class="bg-primary text-white">
                    Harga Reseller
                    </vs-th>
                    <vs-th sort-key="harga_customer"  width="120px" class="bg-primary text-white">
                    Harga Customer
                    </vs-th>
                    <vs-th sort-key="aksi" width="80px" class="bg-primary text-white">
                    Aksi
                    </vs-th>
                </template>

                <template slot-scope="{data}">
                    <vs-tr :key="items.kode" v-for="items in data" >
                        <vs-td :data="items.wilayah">
                            {{items.wilayah}}
                        </vs-td>
                        <vs-td :data="items.harga_stockpoint">
                            {{items.harga_stockpoint.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
                        </vs-td>
                        <vs-td :data="items.harga_reseller">
                            {{items.harga_reseller.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
                        </vs-td>
                        <vs-td :data="items.harga_customer">
                            {{items.harga_customer.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}
                        </vs-td>
                        <vs-td>
                            <vx-tooltip text="Ubah Harga Produk" style="display:inline; z-index: 100000;">
                                <vs-button @click="editHarga(formHarga.kode_product, judulHarga, items.kode, items.harga_stockpoint, items.harga_reseller, items.harga_customer)" class="mr-1" color="warning" type="filled" size="small" icon="create" style="display:inline"></vs-button>
                            </vx-tooltip>
                        </vs-td>
                    </vs-tr>
                </template>
            </vs-table>

            <!-- Popup Add Harga -->
            <vs-popup title="Tambah Harga Produk" :active.sync="popupAddHarga">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <small class="ml-1">Kategori</small><br>
                    <v-select :placeholder="'-- Pilih wilayah --'" label="nama"   v-validate="'required'" name="formHarga.kode_wilayah" data-vv-validate-on="blur" :reduce="wilayah => wilayah.kode" :options="wilayahAll" v-model="formHarga.kode_wilayah"  ></v-select>
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="number" class="w-full" label="Harga Stock Point"  v-validate="'required'" name="formHarga.harga_stockpoint" data-vv-validate-on="blur" v-model="formHarga.harga_stockpoint" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="number" class="w-full" label="Harga Reseller"  v-validate="'required'" name="formHarga.harga_reseller" data-vv-validate-on="blur" v-model="formHarga.harga_reseller" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="number" class="w-full" label="Harga Customer"  v-validate="'required'" name="formHarga.harga_customer" data-vv-validate-on="blur" v-model="formHarga.harga_customer" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" :disabled="!validateFormAddHarga" class="mr-3 mb-2" @click=" addDataHarga(judulHarga)">Simpan</vs-button>
                    </div>
                </div>
            </vs-popup>

            <!-- Popup Edit Harga -->
            <vs-popup title="Ubah Harga Produk" :active.sync="popupEditHarga">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="number" class="w-full" label="Harga Stock Point" v-validate="'required'" name="formHarga.harga_stockpoint" v-model="formHarga.harga_stockpoint" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="number" class="w-full" label="Harga Reseller" v-validate="'required'" name="formHarga.harga_reseller" v-model="formHarga.harga_reseller" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="number" class="w-full" label="Harga Customer" v-validate="'required'" name="formHarga.harga_customer" v-model="formHarga.harga_customer" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" :disabled="!validateFormEditHarga" class="mr-3 mb-2" @click=" editDataHarga()">Simpan</vs-button>
                    </div>
                </div>
            </vs-popup>
        </vs-popup>

         <!-- Popup Data Summary -->
        <vs-popup fullscreen title="Ringkasan Produk" :active.sync="popupDataSummary">
            <!-- Card -->
            <div class="vx-row">
                <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
                    <div class="vx-card overflow-hidden mb-base">
                        <div class="vx-card__collapsible-content vs-con-loading__container">
                            <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                                <div class="avatar bg-rgba-primary p-50">
                                    <span class="inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-success),0.15); width:40px; height:40px">
                                        <div class="avatar-content" >
                                            <i class="feather icon-shopping-cart text-success font-bold font-medium-5 ml-4"></i>
                                        </div>
                                    </span>
                                </div>
                                <div class="truncate">
                                    <h2 class="mb-1 font-bold">{{card.pembelian_blnini}}</h2>
                                    <span>Pembelian</span>
                                </div>
                            </div>
                        </div> 
                    </div>
                </div>   
                <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
                    <div class="vx-card overflow-hidden mb-base">
                        <div class="vx-card__collapsible-content vs-con-loading__container">
                            <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                                <div class="avatar bg-rgba-primary p-50">
                                    <span class="inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-danger),0.15); width:40px; height:40px">
                                        <div class="avatar-content" >
                                            <i class="feather icon-truck text-danger font-bold font-medium-5 ml-4"></i>
                                        </div>
                                    </span>
                                </div>
                                <div class="truncate">
                                    <h2 class="mb-1 font-bold">{{card.terjual_blnini}}</h2>
                                    <span>Penjualan</span>
                                </div>
                            </div> 
                        </div>
                    </div>
                </div>   
                <div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3">
                    <div class="vx-card overflow-hidden mb-base">
                        <div class="vx-card__collapsible-content vs-con-loading__container">
                            <div class="p-6 flex justify-between flex-row-reverse items-center pb-0 mb-10">
                                <div class="avatar bg-rgba-primary p-50">
                                    <span class="inline-flex rounded-full feather-icon select-none relative text-primary" style="background: rgba(var(--vs-warning),0.15); width:40px; height:40px">
                                        <div class="avatar-content" >
                                            <i class="feather icon-box text-warning font-bold font-medium-5 ml-4"></i>
                                        </div>
                                    </span>
                                </div>
                                <div class="truncate">
                                    <h2 class="mb-1 font-bold">{{card.sisa}}</h2>
                                    <span>Sisa</span>
                                </div>
                            </div>
                        </div> 
                    </div>
                </div>   
            </div>
            
            <!-- Table Data Summary -->
            <vx-card>
            <vs-table stripe search pagination  max-items="10" :data="dataSummary">
                 <template slot="header" class="mb-5">
                    <h1 class="mr-2">{{form.barang}} ({{form.sku}})</h1>
                </template>

                <template slot="thead">
                    <vs-th sort-key="nama" width="80%" class="bg-primary text-white">
                    Nama
                    </vs-th>
                    <vs-th sort-key="status" class="bg-primary text-white">
                    Status
                    </vs-th>
                    <vs-th sort-key="qty" width="80px"  class="bg-primary text-white">
                    Qty
                    </vs-th>
                </template>

                <template slot-scope="{data}">
                    <vs-tr :key="items.kode" v-for="items in data" >
                        <vs-td :data="items.nama">
                            {{items.nama}}
                        </vs-td>
                        <vs-td :data="items.tipe">
                            {{items.tipe}}
                        </vs-td>
                        <vs-td>
                            {{items.qty}}
                        </vs-td>
                    </vs-tr>
                </template>
            </vs-table>
            </vx-card>
        </vs-popup>
    </div>
</template>

<script>
export default {
    data() {
        return {
            publicPath: process.env.BASE_URL,
            addNew:false,
            Edit:false,
            popupAddHarga:false,
            popupEditHarga:false,
            popupHarga:false,
            popupDataSummary:false,
            judulHarga:'',
            hargaProduct:[],
            products:[],
            kategoriAll:[],
            wilayahAll:[],
            form: {
                sku:'',
                nama:'',
                barang:'',
                img:null,
                kode_kategori:'',
                kategori:'',
                kode_barang_kategori:''
            },
            formHarga:{
                kode_product:'',
                kode:'',
                kode_wilayah:'',
                harga_stockpoint:'',
                harga_reseller:'',
                harga_customer:''
            },
            dataSummary:[],
            card:{
                pembelian_blnini:'',
                terjual_blnini:'',
                pembelian_blnlalu:'',
                terjual_blnlalu:'',
                sisa:''
            }
            

        }
    },

    computed: {
        validateFormAdd() {
        return !this.errors.any() 
            && this.form.sku != ''
            && this.form.nama != ''
            && this.form.kode_barang_kategori != ''
            // && this.form.img != ''
        },

        validateFormEdit() {
        return !this.errors.any()
            && this.form.sku != ''
            && this.form.barang != ''
            // && this.form.img == ''
            && this.form.kode_kategori != ''
        },
        validateFormAddHarga() {
        return !this.errors.any()
            && this.formHarga.kode_product !=''
            && this.formHarga.kode_wilayah !=''
            && this.formHarga.harga_stockpoint !=''
            && this.formHarga.harga_reseller !=''
            && this.formHarga.harga_customer !=''
        },
        validateFormEditHarga() {
        return !this.errors.any()
            && this.formHarga.kode !=''
            && this.formHarga.harga_stockpoint !=''
            && this.formHarga.harga_reseller !=''
            && this.formHarga.harga_customer !=''
        },
        
    },

    created() {
       this.loadData();
       this.loadKategori();
       this.loadWilayah();
    
    },

    methods:{
        addProduct(){
            this.addNew = true,
            this.form.sku = '',
            this.form.nama = '',
            this.form.img = null,
            this.form.kode_barang_kategori =''
        },

        addHargaProduct(){
            this.popupAddHarga = true,
            this.formHarga.kode_wilayah ='',
            this.formHarga.harga_stockpoint='',
            this.formHarga.harga_reseller ='',
            this.formHarga.harga_customer =''

        },

        loadData(){
            this.$vs.loading();
            this.$http.get('agen/web/produk')
            .then(response => {
                this.products = response.data.message
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});            
        },

        loadKategori(){
            this.$http.get('agen/web/produk/kategori')
            .then(response => {
                this.kategoriAll = response.data.message
            })
            .catch(err=>{
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
        },

        loadWilayah(){
            this.$http.get('agen/web/wilayah')
            .then(response => {
                this.wilayahAll = response.data.message
            })
            .catch(err=>{
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
        },

        handleImage(e){
            const selectedImage = e.target.files[0];
            this.createBase64Image(selectedImage);
        },

        createBase64Image(fileObject){
            const reader = new FileReader();

            reader.onload = (e) => {
                this.form.img = e.target.result;
            };
            reader.readAsDataURL(fileObject);
        },

        addData(){
            this.$vs.loading();
            const postData = {
                sku: this.form.sku,
                nama: this.form.nama,
                img: this.form.img,
                kode_barang_kategori: this.form.kode_barang_kategori.kode,
                // kode_barang_kategori: this.form.kode_barang_kategori.kode,
            }
            // console.log(postData);
            this.$http.post('agen/web/produk/create', postData)
            
            .then(response=> {
                if(response.data.status == "SUKSES"){
                    this.form.sku = '',
                    this.form.nama = '',
                    this.form.img = null,
                    this.form.kode_barang_kategori = '',
                    this.addNew = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },

        editData(kode){

            this.$http.get('agen/web/produk/'+ kode)
            .then(response => {
                this.Edit = true
                this.form.kode = response.data.message[0].kode,
                this.form.sku = response.data.message[0].sku,
                this.form.barang = response.data.message[0].barang,
                this.form.kategori = response.data.message[0].kategori,
                this.form.kode_kategori = response.data.message[0].kode_kategori,
                this.form.img = response.data.message[0].img

            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })  
        },

        updateData(kode){
            this.$vs.loading();
            const updtData = {
                kode: kode,
                sku: this.form.sku,
                nama: this.form.barang,
                kode_barang_kategori: this.form.kode_kategori,
                img: this.form.img
            }
            this.$http.post('agen/web/produk/update', updtData)
            .then(response=> {
                if(response.data.status == "SUKSES"){
                    this.form.sku = '',
                    this.form.nama = '',
                    this.form.img = null,
                    this.form.kode_kategori = '',
                    this.Edit = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },

        popupHargaBtn(kode, barang){
            this.$vs.loading();
            this.popupHarga = true
            this.judulHarga = barang
            this.formHarga.kode_product = kode

            this.$http.get('agen/web/produk/harga/'+ kode)
            .then(response => {
                this.hargaProduct = response.data.message
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});   
        },

        addDataHarga(barang){
            this.$vs.loading();
            const postData = {
                kode: this.formHarga.kode_product,
                kode_wilayah: this.formHarga.kode_wilayah,
                harga_stockpoint: this.formHarga.harga_stockpoint,
                harga_customer: this.formHarga.harga_customer,
                harga_reseller: this.formHarga.harga_reseller
            }

            this.$http.post('agen/web/produk/harga/create', postData)
            
            .then(response=> {
                if(response.data.status == "SUKSES"){
                    this.popupAddHarga = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();

                        this.popupHarga = true
                        this.judulHarga = barang
                        // this.formHarga.kode_product = this.formHarga.kode_product

                        this.$vs.loading();
                        this.$http.get('agen/web/produk/harga/'+ this.formHarga.kode_product)
                        .then(response => {
                            this.hargaProduct = response.data.message
                        })
                        .catch(err=>{
                            this.$vs.loading.close()
                            this.$swal.fire(
                                'Gagal !',
                                err.response.data.message,
                                'error'
                                )
                            })
                        .finally(() => {this.$vs.loading.close()});   

                        this.formHarga.kode_product = ''
                        this.formHarga.kode_wilayah = ''
                        this.formHarga.harga_stockpoint = ''
                        this.formHarga.harga_customer = ''
                        this.formHarga.harga_reseller =''
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },
        
        editHarga( kode_product, judulHarga, kode, harga_stockpoint, harga_reseller, harga_customer){
            this.popupEditHarga = true
            this.formHarga.kode = kode
            this.formHarga.harga_stockpoint = harga_stockpoint
            this.formHarga.harga_reseller = harga_reseller
            this.formHarga.harga_customer = harga_customer
            this.formHarga.kode_product = kode_product
            this.formHarga.judulHarga = judulHarga
            // console.log(kode_product)
        },

        editDataHarga(){

            this.$vs.loading();
            const updtData = {
                kode: this.formHarga.kode,
                harga_reseller: this.formHarga.harga_reseller,
                harga_customer: this.formHarga.harga_customer,
                harga_stockpoint: this.formHarga.harga_stockpoint
            }

            this.$http.post('agen/web/produk/harga/update', updtData)
            .then(response=> {
                if(response.data.status == "SUKSES"){
                    this.popupEditHarga = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                        this.popupHarga = true
                        // this.judulHarga = barang
                        this.formHarga.kode_product = this.formHarga.kode_product

                        this.$vs.loading();
                        this.$http.get('agen/web/produk/harga/'+ this.formHarga.kode_product)
                        .then(response => {
                            this.hargaProduct = response.data.message
                        })
                        .catch(err=>{
                            this.$vs.loading.close()
                            this.$swal.fire(
                                'Gagal !',
                                err.response.data.message,
                                'error'
                                )
                            })
                        .finally(() => {this.$vs.loading.close()});   

                        this.formHarga.kode = ''
                        this.formHarga.harga_stockpoint = ''
                        this.formHarga.harga_customer = ''
                        this.formHarga.harga_reseller =''
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});
        },

        popupDataSummaryBtn(kode, sku, barang){
            this.$vs.loading();
            this.popupDataSummary = true
            this.form.sku = sku
            this.form.barang = barang


            this.$http.get('agen/web/produk/summary/'+ kode)
            .then(response=> {
                this.dataSummary = response.data.message.table
                this.card.pembelian_blnini = response.data.message.card.pembelian_blnini
                this.card.terjual_blnini = response.data.message.card.terjual_blnini
                this.card.pembelian_blnlalu = response.data.message.card.pembelian_blnlalu
                this.card.terjual_blnlalu = response.data.message.card.terjual_blnlalu
                this.card.sisa = response.data.message.card.sisa
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()});

        }
         
    }

}
</script>

